.AllTeamMembers {
  width: 100%;
  text-align: center;
  margin-top: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.AllTeamMembers h1 {
  color: white;
  text-shadow: 2px 5px 5px white;
  text-align: center;
  margin: 10px;
}
.myteam {
  width: 100%;
  text-align: center;
}
.card {
  width: 250px;
  height: 300px;
  margin: 20px auto;
  background-color: transparent;
  border: 3px solid rgb(255, 255, 255);
  border-radius: 10px;
  box-shadow: 0 2px 4px rgba(245, 241, 241, 0.1);
  overflow: hidden;
  position: relative;
  transition: box-shadow 0.3s ease;
  display: inline-block;
  transition: 0.2s ease-in-out;
  color: white;
  margin: 5px;
}

.card:hover {
  box-shadow: 2px 4px 10px whitesmoke;
  transform: scale(1.01);
}

.card img {
  width: 100%;
  height: 150px;
  object-fit: contain;
}

.card-content {
  padding: 20px;
  text-align: center;
  width: 100%;
}

.card-content h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.card-content p {
  font-size: 14px;
  color: #d1cfcf;
}

.card-social-icons {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  transition: opacity 0.3s ease;
  opacity: 0;
}

.card:hover .card-social-icons {
  opacity: 1;
}

.card-social-icons a {
  display: inline-block;
  margin: 0 5px;
  color: #888;
  transition: color 0.3s ease;
}

.card-social-icons a:hover {
  color: #555;
}

.Orderwise ,.sacOrderWise{
  display: flex;
  justify-content: center;
}

@media (max-width: 768px) {
  .AllTeamMembers {
    margin-top: 15%;
    text-align: center;
    margin: 25px;
  }
 .card .center{
  display: block;

 }
  
  .myteam .center2 {
    display: inline-block;
  }
  .Orderwise{
    flex-direction: column;
  }
  .sacOrderWise{
    flex-direction: column;
  }


}
