nav {
  height: 0;
  width: 100%;
}
.Navband {
  line-height: 60px;
  font-size: 30px;
  padding: 20px;
}

.Navband img {
  height: 50px;
  width: 50px;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid white;
  transition: 0.5s ease;
}

.Navband img:hover{
  height: 52px;
  width: 52px;
}

nav ul {
  list-style-type: none;
  float: right;
  position: relative;
}
nav ul li {
  display: inline-block;
  padding: 20px;
}
a {
  text-decoration: none;
  color: white;
}

nav ul li a:hover,
nav ul li a.active {
  color: orangered;
  border-bottom: 3px solid orangered;
  transition: 0.2s ease;
}

nav ul ul {
  position: absolute;
  display: none;
  left: 85px;
}

nav ul ul li {
  display: block;
  padding: 10px 0;
}
nav ul li:hover > ul {
  display: block;
}

#checkbtn {
  font-size: 25px;
  color: white;
  float: right;
  line-height: 60px;
  padding-right: 20px;
  cursor: pointer;
  display: none;
  z-index: 9999;
}

#check {
  display: none;
}

@media (max-width: 900px) {
  nav ul li {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  nav ul {
    position: fixed;
    top: 0px;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(
      to bottom,
      rgba(245, 95, 40, 0.884),
      black
    );
    z-index: 100;
    text-align: center;
    left: 100%;
  }
  nav ul li a {
    color: white;
    font-size: 30px;
  }

  nav ul li a.active {
    color: white;
    border-bottom: 5px solid white;
  }

  nav ul li {
    display: block;
  }

  #check {
    display: none;
  }

  #checkbtn {
    display: block;
  }
  .Navband {
    font-size: 1.5rem;
  }

  #check:checked ~ ul {
    left: 100%;
  }
}
