.contact-form {
  max-width: 600px;
  margin: 100px auto;
  padding: 20px;
  color: white;
  background-image: linear-gradient(rgb(238, 32, 32), white);
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 999;
 
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  font-weight: bold;
}

input,
textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  resize: none;
}

.btn {
  background-color: orangered;
  color: white;
  padding: 10px 20px;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  opacity: 0.7;
}

.btn:hover {
  background-color: orangered;
  opacity: 1;
}


@media (max-width:768px){
  .contact-form{
    margin: 10px;
  }
}