/* Set the background image and style */
.HomeContainer {
  background-image: url("./components/atrimages/mainbgimg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
}

/* Main section Home page Style */

/* Center the container */
.bodycontainer {
  width: 100%;
  text-align: center;
  position: static;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh + 50px); /* Adjust based on the navbar height */
}

/* Style the title */
.title {
  font-size: 3rem;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  animation: fadeIn 2s ease-in-out infinite;
  margin-top: -200px;
}

.title img {
  width: 250px;
}

/* Style the subtitle */
.subtitle {
  color: #fff;
  font-size: 16px;
  opacity: 0.8;
}

.address-text {
  color: #fff;
  writing-mode: vertical-lr;
  text-transform: translate(180deg);
  position: absolute;
  right: 10px;
  top: 30%;
  font-size: 14px;
}

/* Style the icon container */
.icon-container {
  position: absolute;
  bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: px;
}

.icon-container span {
  color: #fff;
  font-size: 14px;
  margin: 5px;
  opacity: 0.5;
}

/* Coloring the iocn */
.f,
.i,
.y,
.s {
  padding: 5px;
  font-size: 36px;
}
.f {
  color: rgb(56, 135, 253);
}
.i {
  color: rgb(236, 91, 241);
}
.y {
  color: orangered;
}
.s {
  color: rgba(238, 238, 87, 0.849);
}

.eventdate {
  color: #fff;
  font-family: 'Cherry Bomb One', cursive;
  margin-bottom: 10px;
}
.eventreg-btn {
  margin-top: 10px;
  border: 2px solid rgb(241, 69, 135);
  background-image: linear-gradient(
    to bottom,
    rgba(208, 1, 250, 0.3),
    rgba(255, 68, 0, 0.3)
  );
  padding: 15px 60px;
  color: whitesmoke;
  clip-path: fill-box;
  cursor: pointer;
  clip-path: polygon(90% 1%, 100% 10%, 100% 100%, 10% 100%, 0 90%, 0 0);
  transition: 0.2s ease;
  margin-right: 20px;
}

.scudule,
.reg {
  background-color: transparent;
}

.eventreg-btn:hover {
  color: #fff;
  background-image: linear-gradient(
    to bottom,
    rgba(208, 1, 250, 0.699),
    rgba(255, 68, 0, 0.74)
  );
}

/* College Location */
.location {
  color: white;
  font-size: larger;
  text-transform: capitalize;
  margin-top: 5px;
}
/* Define the fade-in animation */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Make the page responsive */
@media (max-width: 768px) {
  .title {
    font-size: 2rem;
  }
  .navbar-brand img {
    width: 40px;
    height: 40px;
  }

  .navbar li {
    display: none;
    display: block;
    flex-direction: column;
  }
  .icon-container .f,
  .i,
  .y,
  .s {
    font-size: 2rem;
  }
  .address-text {
    font-size: 13px;
    top: 35%;
  }
  .icon-container span {
    font-size: 12px;
  }
}

@media (max-width: 470px) {
  .address-text {
    font-size: 12px;
    right: 5px;
    top: 37%;
  }
}

/* About Section Style */

.aboutcontainer {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  flex: 1;
  padding: 10px 20px;
  background-color: #000000;
  color: #fff;
  text-align: center;
  padding: 120px 20px;
}

.column img {
  max-width: 85%;
  height: auto;
  border-radius: 15px;
  box-shadow: 0 5px 25px rgb(100, 214, 248);
}

.column p {
  padding: 5px 0;
  text-align: justify;
}

@media (max-width: 768px) {
  .column {
    flex-basis: 100%;
    padding-top: 50px;
    padding-bottom: 20px;
  }
  

  .rightText p{
    font-size: 14px;
}
}
