.bfooter {
  width: 100%;
  text-align: center;
}
footer {
  background-image: linear-gradient(to top, rgba(252, 68, 68, 0.74), black);
  width: 100%;
  height: auto;
  color: white;
  text-align: center;
  margin-top: 40px;
  box-shadow: 0px 0px 40px 40px #000000;
}

footer .leftlogo img{
  width: 52px;
  height: 52px;
  object-fit: cover;
  border: 1px solid white;
  border-radius: 50%;
  
}

.frow {
  width: 100%;
  display: flex;
  padding: 20px;
}
.left,
.center,
.right {
  width: 33.3%;
}

.clinks a {
  display: block;
  text-decoration: none;
  font-size: 16px;
  color: white;
}
.icons {
  display: inline;
}

.ficons a {
  display: inline-block;
  font-size: 30px;
  text-decoration: none;
  margin-right: 10px;
  color: rgb(184, 102, 102);
  transition: 0.2s ease;
}

.fcontent h1{
  font-size: 30px;
}

.ficons a:hover {
  color: orangered;
  transform: scale(1.1);
}

.copyright {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: rgb(192, 51, 51);
  opacity: 0.5;
}

.fccenter a{
 color: whitesmoke;
 opacity: 1;
 text-decoration: underline;
}

@media (max-width: 768px) {
  .frow{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 14px;
  }

  .fcol {
    margin-bottom: 20px;
  }
  .fcontent h1{
    font-size: 25px;
  }
  .center a,.right a{
    margin-left: -30px;
  }
  .copyright {
    flex-direction: column;
  }
  .clinks a {
    font-size: 14px;
  }
}

@media (max-width:468px){
  .fcontent h1{
    font-size: 18px;
  }
}
