.gCommitteWrapper {
    width: 100%;
    text-align: center;
    display: flex;
    margin:auto 0;
    flex-direction: column;
    position: relative;
  }
  .gcommitteTitle{
    padding: 10px;
  }
  .gcommitteTitle h1{
    color: #fff;
    float: left;
    font-size: 40px;
    
  }
  .gcommitteTitle p{
    font-size: 16px;
    float: left;
    color: #fff;
    text-align: left;
  }
  .gcard {
    position: relative;
    text-align: justify;
    width: 100%;
    max-width: 430px;
    height: auto;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    display: inline-block;
    margin: 5px;
    box-sizing: border-box;
  }
  
  .gcard img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    transition: filter 0.5s ease;
    transition: 0.4s ease;
  }
  
  .gcard:hover img {
    filter: blur(5px);
  }
  
  .gcard .gcontent {
    position: absolute;
    bottom: -43%;
    width: 100%;
    padding: 15px;
    background-image: linear-gradient(to bottom,rgba(214, 163, 163, 0.877),orangered);
    color: #fff;
    transition: bottom 0.5s ease;
  }
  
  .gcard:hover .gcontent {
    bottom: 0;
  }
  
  .gcard:hover h1,
  h3 {
    font-size: 20px;
  }
  
  @media (max-width: 768px) {
    .gCommitteWrapper {
      flex-direction: column;
    }
    .gcard {
      max-width: 95%;
    }
    .gcard .gcontent {
      bottom: -5%;
    }
    .gcard img{
        height: 400px;
    }

  }
  