.AllSponsors {
  width: 100%;
  text-align: center;
  margin-top: 1%;
  display: inline-block;
}

.sponsorcard {
  width: 100%;
  max-width: 300px;
  margin-top: 10px;
  margin-left: 20px;
  border-radius: 0 25px;
  clip-path: polygon(90% 1%, 100% 10%, 100% 100%, 10% 100%, 0 90%, 0 0);
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transition: 0.2s ease;
  object-fit: cover;
  background-color: white;
}

.sponsorcard img {
  width: 100%;
  object-fit: contain;
  height: 180px;
  transition: 0.2s ease-in-out;
}
.sponsorcard img:hover {
  transform: scale(1.15);
}

.sponsorcard:hover {
  transform: translateY(-8px);
}

.sposorpnglogo {
  width: 200px;
  height: auto;
}

.sponsor-btn {
  padding: 15px 45px;
  margin-top: 10px;
  border: 2px solid rgb(198, 57, 241);
  background-image: linear-gradient(
    to bottom,
    rgba(208, 1, 250, 0.3),
    rgba(255, 68, 0, 0.3)
  );
  cursor: pointer;
  font-weight: 700;
  font-size: large;
  background-color: transparent;
  color: #fff;
  clip-path: polygon(90% 1%, 100% 10%, 100% 100%, 10% 100%, 0 90%, 0 0);
  transition: 0.3s ease;
}

.sponsor-btn:hover {
  background-image: linear-gradient(
    to bottom,
    rgba(208, 1, 250, 0.699),
    rgba(255, 68, 0, 0.74)
  );
  transform: translateY(-5px);
}

@media (max-width: 768px) {
  .sponsorcard {
    max-width: 42%;
    display: inline-block;
    margin: 15px;
  }
  .AllSponsors {
    margin-top: 10%;
  }
}

@media (max-width: 468px) {
  .sponsorcard {
    max-width: 82%;
    display: inline-block;
    margin: 15px;
  }
  .AllSponsors {
    margin-top: 10%;
  }
}

