.Gallarwrapper {
  width: 100%;
  text-align: center;
  margin-top: 80px;
  color: #fff;
}

.eimg {
  max-width: 100%;
  margin: 5px;
  display: inline-block;
  transition: 0.2s ease;
  box-sizing: border-box;
  border: 1px solid whitesmoke;
  border-radius: 10px;
}
.eimg img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 0px 2px 8px rgb(231, 224, 221);
}

.eimg:hover {
  transform: scale(1.02);
  color: gray;
}


/* YearWise  div Style */
.Year{
  display: flex;
  justify-content: center;
  align-items: center;
}

/* End of YearWise style */

@media (max-width:768px){
  .Gallarwrapper{
  text-align: center;

  }
  .eimg img{
    width: 100%;
  height: 200px;
  padding: 0;
  
  }
}

@media (max-width:580px){
  .eimg img{
    height: 250px;
    width: 100%;
  }
}