.EventsWrapper {
  width: 100%;
  text-align: center;
  position: static;
  display:inline-block;
}

.eventLogobg {
  width: 100%;
  object-fit: cover;
  position: relative;
}

.eventLogo {
  position: relative;
  width: 20%;
  object-fit: cover;
  z-index: 1; 
}

.event-btn {
  /* position: absolute; */
  border: none;
  outline: none;
  padding: 20px 45px;
  margin: 15px;
  border-radius: 20px;
  background-color: rgba(212, 0, 255, 0.637);
  color: white;
  font-weight: 800;
}
.erow {
  display: flex;
  align-items: center;
  justify-content: center;
}

.EventsWrapper h1 {
  text-align: center;
  color: white;
  text-transform: capitalize;
  font-size: 2.2rem;
}
.ecol {
  width: 30%;
  margin: 20px;
  border: 3px solid white;
  border-radius: 10px;
  background-color: orangered;
  transition: .4s ease;
}

.ecol img {
  width: 100%;
  object-fit: cover;
  position: relative;
}
.ecol:hover{
  transform:scale(1.05);
}

@media (max-width: 768px) {
  .erow {
    flex-direction: column;
  }
  .ecol {
    width: 80%;
  }
  .EventsWrapper h1{
    font-size: 2rem;
  }
}
